<template>
    <div class="cart">
        <HeaderComp></HeaderComp>
        <div class="cart_wrapper">
            <h1 style="font-weight: bold; align-self: flex-start;">Товары в корзине</h1>
            <div class="cart_table">
                <div class="cart_elem" v-for="prod of cart" :key="prod.id">
                    <div class="cart_elem_image">
                        <img :src="prod.images[0].path" height="100" v-if="prod.images.length > 0">
                        <img src="@/assets/icons/product_none.png" height="100" v-else>
                    </div>
                    <div class="cart_elem_meta_group">
                        <span class="cart_elem_name">{{ prod.name }}</span>
                        <span class="cart_elem_prop">Размер: {{ $Helper.findSizeNameByID(prod,prod.size)}}</span>
                        <span class="cart_elem_prop" v-for="meta in JSON.parse(prod.meta)" :key="meta.name">{{$Helper.translateMetaKey(meta.name)}}: {{ meta.value }}</span>
                        <span class="cart_elem_prop amount">Количество:  <button @click="removeFromCart(prod)">-</button> <span>{{ prod.amount}}</span> <button @click="addToCart(prod)">+</button></span>
                
                    </div>
                    <div class="cart_elem_meta_group">
                        <div class="cart_elem_meta">
                            <span class="cart_elem_price">{{ ~~prod.price * ~~prod.amount }} р.</span>
                        </div>
                    </div>
                </div>
            </div> 
            <button class="go_to_order" @click="$router.push('/order')" v-if="$CoreAPI.client_getCart().length > 0">Оформить заказ</button>
            <span v-if="$CoreAPI.client_getCart().length < 1" style="align-self: flex-start; font-size: 0.8em; margin-top: 1vh;">Корзина пуста</span>
        </div>
        
    </div>
</template>

<script>
import HeaderComp from '@/components/HeaderComp.vue';


    export default {
        name: 'CartView',
        mounted(){
            
        },
        data(){
            return {
                cart : this.$CoreAPI.client_getCart()
            }
        },
        components:{
            HeaderComp
        },
        methods : {
            removeFromCart(id){
                this.$CoreAPI.client_updateProdAmount(id.id,id.size,-1)
                this.cart = this.$CoreAPI.client_getCart();
            },
            addToCart(id){
                this.$CoreAPI.client_updateProdAmount(id.id,id.size,+1)
                this.cart = this.$CoreAPI.client_getCart();
            }
        }
    }
</script>
<style scoped>
@import url("@/assets/styles/__cart.scss");
    .cart{
      
        
    }
    body{
        overflow: hidden;
    }
    .amount{
        display: flex;
        align-items: center;
        column-gap: 1vw;
    }
    .cart_wrapper{
        
        padding: 10px;
        width: 600px;
        font-size: 20px;
        margin: 0 auto;
        background-color: #f8f8f8;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .cart_elem{
        display: flex;
        width: 500px;
        justify-content: space-between;
    }
    button:not(.go_to_order){
        width: 20px;
        border-radius: 50%;
        padding: 1%;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 20px;
    }
    .go_to_order{
        background-color: #fff;
        padding: 10px;
        margin-top: 20px;
    }
</style>